.circles {
    width: 400px;
    height: 400px;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}


.circle1 {
    width: 300px;
    height: 300px;
    background: linear-gradient(45deg, #ff0099, #7a0ed6);
    border-radius: 50%;
    position: absolute;
    top: 50px;
    left: -600px;
}

.circle2 {
    width: 200px;
    height: 200px;
    background: linear-gradient(45deg, #ff237b, #f64838);
    border-radius: 50%;
    position: absolute;
    bottom: -30px;
    left: 300px;
}
